import * as React from 'react';
import { Table, Input, Row, Col, Button, Modal, Alert } from 'antd';
import axios from 'axios';
import moment from 'moment'
import './index.scss';

interface IState {
    grant_type: string,
    appid: string,
    secret: string,
    has_accesstoken: boolean,
    wechat_info: ILocalStorage
}
interface ILocalStorage {
    grant_type: string,
    appid: string,
    secret: string,
    access_token: string,
    expires_time: string
}
export default class Add extends React.Component<{ history: any }, IState>{
    constructor(props: { history: any }) {
        super(props);
        this.state = {
            grant_type: 'client_credential',
            appid: '',
            secret: '',
            has_accesstoken: false,
            wechat_info: {
                grant_type: '',
                appid: '',
                secret: '',
                access_token: '',
                expires_time: ''
            }
        };
        if (typeof (Storage) !== 'undefined') {
            console.log('浏览器支持LocalStorage！')
        } else {
            Modal.error({
                title: '浏览器版本过低',
                content: '您的浏览器不支持LocalStorage，请更换现代浏览器（如 Chrome）',
                onOk: () => {
                    // this.props.history.push('/home')
                    this.props.history.push({ pathname: '/home' })
                }
            })
        }
    };

    public componentDidMount() {
        const wechatInfo = JSON.parse(localStorage.getItem('wechat_account') as string)
        if (wechatInfo) {
            this.setState({
                has_accesstoken: true,
                wechat_info: wechatInfo
            })
        }
    }

    public render() {
        const { has_accesstoken } = this.state;
        if (has_accesstoken) {
            const { grant_type, appid, secret, access_token, expires_time } = this.state.wechat_info;

            const columns = [{
                title: 'access_token',
                dataIndex: 'access_token',
                key: 'access_token',
                width: '20%'
            }, {
                title: 'grant_type',
                dataIndex: 'grant_type',
                key: 'grant_type',
                width: '15%'
            }, {
                title: 'appid',
                dataIndex: 'appid',
                key: 'appid',
                width: '15%'
            }, {
                title: 'secret',
                key: 'secret',
                dataIndex: 'secret',
                width: '15%'
            }, {
                title: '过期时间',
                key: 'expires_time',
                dataIndex: 'expires_time',
                width: '20%',
                render: (text: any) => {
                    const date = moment(text).format("YYYY-MM-DD HH:mm:ss");
                    const now = new Date().getTime();
                    const isExpire = now > text ? '已过期' : '未过期';
                    const style = {
                        color: isExpire === '已过期' ? 'red' : 'green'
                    };
                    return (
                        <span>
                            <span>{date}</span><br />
                            (<span style={style}>{isExpire}</span>)
                        </span>
                    )
                }
            }, {
                title: '操作',
                key: 'action',
                render: (text: any, record: any) => (
                    <span>
                        <Button className='operate' type='primary' onClick={() => this.bindWechat(record.appid, record.secret)}>刷新</Button>
                        <Button className='operate' type='danger' onClick={this.deleteWechat}>解绑</Button>
                    </span>
                ),
            }];
            const data = [{
                key: '1',
                access_token,
                grant_type,
                appid,
                secret,
                expires_time,
            }];
            return (
                <div>
                    <Alert style={{ marginBottom: 10 }} message="access_token有效期为 2 小时，过期请点击 “刷新” 重新获取！" type="success" />
                    <Table columns={columns} dataSource={data} pagination={false} />
                </div>
            )
        } else {
            return (
                <div>
                    <Row type='flex' justify='center' className='row-item'>
                        <Col span={10}>
                            <Input type='text' addonBefore='grant_type' disabled value={this.state.grant_type} />
                        </Col>
                    </Row>
                    <Row type='flex' justify='center' className='row-item'>
                        <Col span={10}>
                            <Input type='text' addonBefore='appid' name='appid' value={this.state.appid} onChange={this.getInputValue} />
                        </Col>
                    </Row>
                    <Row type='flex' justify='center' className='row-item'>
                        <Col span={10}>
                            <Input type='text' addonBefore='secret' name='secret' value={this.state.secret} onChange={this.getInputValue} />
                        </Col>
                    </Row>
                    <Row type='flex' justify='center' className='row-item'>
                        <Button type='primary' onClick={() => this.bindWechat()}>绑定微信开发者帐号</Button>
                    </Row>
                </div>
            )
        }
    };

    private getInputValue = (e: any) => {
        switch (e.target.name) {
            case 'appid':
                this.setState({
                    'appid': e.target.value
                })
                break;
            case 'secret':
                this.setState({
                    'secret': e.target.value
                })
                break;
            default:
                break;
        }

    };

    private bindWechat = (appidLocal?: string, secretLocal?: string) => {
        const { grant_type, appid, secret } = this.state;
        if ((!appid || !secret) && (!appidLocal && !secretLocal)) {
            Modal.error({
                title: '帐号绑定',
                content: '请填写完整信息！'
            })
            return
        }
        axios.get(`/api/wechat/cgi-bin/token`, {
            params: {
                grant_type,
                appid: appidLocal ? appidLocal : appid,
                secret: secretLocal ? secretLocal : secret
            }
        }).then(res => {
            if (!res.data.errcode) {
                const expiresTime = new Date().getTime() + (res.data.expires_in) * 1000;
                const wechatAccount = {
                    grant_type,
                    appid: appidLocal ? appidLocal : appid,
                    secret: secretLocal ? secretLocal : secret,
                    access_token: res.data.access_token,
                    expires_time: expiresTime
                }
                localStorage.setItem('wechat_account', JSON.stringify(wechatAccount))
                Modal.success({
                    title: '帐号绑定',
                    content: appidLocal ? 'access_token刷新成功！' : '微信开发者帐号绑定成功',
                    onOk: () => {
                        window.location.reload()
                    }
                })
            } else {
                Modal.error({
                    title: '帐号绑定',
                    content: res.data.errmsg,
                    onOk: () => {
                        window.location.reload()
                    }
                })
            }
        }).catch(err => {
            Modal.error({
                title: '帐号绑定',
                content: '网络错误！',
                onOk: () => {
                    window.location.reload()
                }
            })
        });
    };

    private deleteWechat = () => {
        Modal.confirm({
            title: '帐号解绑',
            content: '确定要解绑该帐号吗？',
            onOk: () => {
                localStorage.removeItem('wechat_account');
                window.location.reload()
            }
        })
    }
}


